import dayjs from "dayjs";
export function createScript(src) {
    const script = document.createElement('script');
    script.setAttribute('src', src);
    return script;
}


export function createLink(href) {
    const link = document.createElement('link');
    link.setAttribute('rel', 'stylesheet');
    link.setAttribute('type', 'text/css');
    
    //  manifest  web_accessible_resources 
    link.setAttribute('href', href);
    link.setAttribute('crossorigin', 'anonymous');
    return link;
}
 
 
// 
export function convertTimeToHumanReadable(dateTime) {
    let date = dayjs(dateTime);
    let curDate = dayjs();
	
	// 
	let minC = curDate.diff(date, 'minute', true);
	// 
	let hourC = curDate.diff(date, 'hour', true);
	// 
	let dayC = curDate.diff(date, 'day', true);
	// 
	let weekC = curDate.diff(date, 'week', true);
	// 
	let monthC = curDate.diff(date, 'month', true);
 
    if(minC < 5) {
        return `<span class="__rencent_update"></span>`;
    } else if (minC < 60) {
        return `<span class="__rencent_update">1</span>`;
    } else if (hourC < 24) {
        return `<span class="__rencent_update">1</span>`;
    } else if (dayC < 7) {
        return `<span class="__rencent_update">${parseInt(dayC)}</span>`
    } else if (monthC < 1) {
        return `${parseInt(Math.ceil(weekC))}`
    } else if (monthC <= 2) {
        return `2`
    } else if (monthC <= 3) {
        return `3`
    } else {
        return '3';
    }

}
 